.page {
	position: absolute;
	right: 0;
	left: 0;
	height: 100%;
}

.page-enter {
	opacity: 0;
	transform: scale(1.1);
}

.page-enter-active {
	opacity: 1;
	transform: scale(1);
	transition: opacity 300ms, transform 300ms;
}

.page-exit {
	opacity: 1;
	transform: scale(1);
}

.page-exit-active {
	opacity: 0;
	transform: scale(1.1);
	transition: opacity 300ms, transform 300ms;
}
